@import url('https://fonts.googleapis.com/css?family=M+PLUS+Rounded+1c');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  font-family: "M PLUS Rounded 1c";
}

body{
  background: url('muesli-3186256_1920.jpg') no-repeat top center;
  background-size: cover;
  height: 100vh;
}

h1 {
  font-size:28px;
  text-align: center;
  margin:30px 0 0 0;
}
.wrapper{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  padding: 0 20px;
}

.contact-form{
  display: flex;
  max-width: 850px;
  margin: 0 auto;
  background: rgba(0,0,0,0.8);
  padding: 30px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0,0,0,0.3);
}

.input-fields{
  display: flex;
  flex-direction: column;
  margin-right: 4%;
}

.input-fields,
.output{
  width: 48%;
}

.input-fields .input,
.output {
  /* margin: 10px 0; */
  background: transparent;
  border: 0px;
  /* border-bottom: 2px solid #c5ecfd; */
  padding: 10px;
  width: 100%;
}
input{
 margin:10px 0;
 background: transparent;
 border:0;
 border-bottom: 2px solid #c5ecfd;
 padding:10px;
 color: #c5ecfd;
 width: 100%;
}
input::placeholder{
  color: rgba(197,236,253,0.5);
}

.output {
  height: 212px;
  color: #c5ecfd;
}
.output p{
  margin: 10px 0;
}
::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #c5ecfd;
}
::-moz-placeholder {
  /* Firefox 19+ */
  color: #c5ecfd;
}
:-ms-input-placeholder {
  /* IE 10+ */
  color: #c5ecfd;
}

.btn {
  margin-top:10px;
  background: #39b7dd;
    text-align: center;
    padding: 15px;
    border-radius: 5px;
    color: #fff;
    cursor: pointer;
    text-transform: uppercase;
}

@media screen and (max-width: 600px){
  h1{
    font-size: 24px;
    margin:5px 0;
  }
  .contact-form{
    flex-direction: column;
  }
  input{
    margin:5px 0;
    padding:5px;
  }
  .btn{
    margin-top:5px;
  }
  .input-fields,
  .output{
    width: 100%;
  }
  .output{
    font-size:0.8rem;
  }
  .output p{
    margin:0px;
    line-height:1.5;
  }
}
